<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Reject') }}
                        </h5>
                        <button v-on:click="closeRejectPopUp" class="close"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from "../../service/DataService";

export default {

    props: {

        specificDetails: {
            type: Object
        }

    },

    data() {

        return {
            details: {},
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Reject'),
                            name: "Reject",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        }

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                },
                {
                    type: "button",
                    class: "btn btn-danger text-white",
                    text: "&nbsp;<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.closeRejectPopUp
                }
            ],

        }
    },


    created: function () {
        var vm = this;
        vm.details = vm.specificDetails;
        console.log("details", vm.details);
    },


    methods: {

        closeRejectPopUp: function () {
            debugger;
            this.$emit('closeRejectPopUp');

        },

        onSubmit: function (values) {
            debugger;
            var vm = this;
            var url = `batchId=${vm.details.RequisitionBatchId}&requisitionCancelledBy=assigner&comment=${values.Reject}`
            DataService.UpdateRequisitionStatus(url).then((response) => {
                if (response.data != null) {
                    this.employe = response.data;
                    this.showProductDropdown = true;
                } else {
                    this.employe = "";
                }
            });



        }
    }







}
</script>

<style ></style>