import DataService from "../../service/DataService";

let buttons = [
  {
    title: "Add Check Out Request",
    iconClass: "fa fa-plus",
    callbackfunction: "AddCheckOut",
    additionalClass: "",
    isdisabled: false,
    isvisible: true,
    checkPrivilege: false,
  },
];

let leftsearchSchema = [
  {
    fieldName: "Product Name",
    fieldType: "radio",
    fieldIcon: "fa-user",
    paramName: "ASSETID",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
  {
    fieldName: "Unique Name",
    fieldType: "ddl-check",
    fieldIcon: "fa-server",
    paramName: "catalogId",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
];

let listActions = [
  {
    title: "Reject",
    href: "",
    icon: "fa fa-times-circle action-icon text-danger",
    additionalSpan: "",
  },
];

function bindleftCommonSearchdropdown(instance, modulename, filtername) {
  var vm = instance;
  if (filtername == "Product_Name") {
    DataService.GetAssetType().then((response) => {
      vm.json = response.data.data;
      if (vm.json != null) {
        vm.json.forEach((item) => {
          let obj = {
            name: item.NAME,
            value: item.ASSETID,
          };
          vm.leftSearchFields[0].listOptions.push(obj);
        });
        return;
      }
    });
  }
}

export default {
  buttons,
  leftsearchSchema,
  listActions,
  bindleftCommonSearchdropdown,
};
